.quiz-container {
  position: relative;
  margin: 50px auto 20px;
  width: 95%;
  max-width: 650px;
  max-height: 350px;
  border: 1px solid #eee;
  box-shadow: 0px 2px 5px 2px rgba($color: #000000, $alpha: 0.05);
}

.join-screen {
  width: 100%;
  height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  text-align: center;
  padding: 20px;
}

.quiz-screen .question {
  .progress-bar {
    width: 100%;
    height: 5px;
  }

  .question-count {
    margin: 20px 0px;
    text-align: center;
    color: #555;
  }

  .main {
    padding: 20px 40px;

    span {
      margin-top: 15px;
      color: #555;
    }

    p {
      margin-top: 5px;
      color: #111;
    }

    .options {
      margin: 40px 0px 20px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 20px;

      .option {
        width: calc(50% - 20px);
        border: 1px solid #bbb;
        padding: 10px;
        border-radius: 5px;
        cursor: pointer;
        
      }

      .option-active{
        background: #1428cd;
        color: #fff;
        }
    }
  }

  .control {
    padding: 10px 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-top: 1px solid #eee;
  }
}

.result-screen{
 width: 100%;
 height: 350px;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 gap: 20px;   
}

.quiz-screen .question .active {
  animation: progress 10s linear forwards;
}


@keyframes progress {
  0% {
    width: 0%;
    background: #555;
  }
  79% {
    width: 79%;
    background: #555;
  }
  80% {
    width: 80%;
    background: orangered;
  }
  100% {
    width: 100%;
    background: orangered;
  }
}
