.team-member-card{
    height: 300px;
    width: 230px;
    margin: 15px;
    border-radius: 5px;
    // background: #f2f2f2!important;
    background-color: #2a2a2a!important;
    // box-shadow:  5px 5px 8px #151515,
    //             -5px -5px 8px #1f1f1f;
    padding: 15px;
    color: rgb(180, 180, 180)!important;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    text-transform: capitalize;

    position: relative;

    

    @media screen and (max-width:1100px) {
        width: 45%;
    }

    @media screen and (max-width:800px) {
        width: 100%;
    }

    @media screen and (max-width:480px) {
        width: 100%;

        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        margin: 10px 0;
        height: 145px;
        padding: 10px 15px;
    }

    @media screen and (max-width:350px){
        height: 135px;
    }

    .card-side-line{
        height: 90px;
        width: 100%;
        // transform: translateX(-98%);
        width: 5px;
        background-color: #ff8800;
        position: absolute;
        left: 0;
        top: 0;

        transition: .15s ease-in-out 0s;

        @media screen and (max-width:480px) {
            height: 100%;
        }
    }

    .member-avatar{
        margin-top: 20px;

        @media screen and (max-width:480px) {
            margin-top: 0px;
        }
    }

    .member-info{
        // margin-left: 15px;
        margin-top: 10px;
        height: 100%;
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        position: relative;

        @media screen and (max-width:480px) {
            align-items: flex-start;
            margin-left: 15px;
        }

        .member-name{
            font-size: 1rem;
        }

        .member-role{
            font-size: .7rem;
            text-align: center;

            @media screen and (max-width:480px) {
                text-align: left;
            }
        }

        .member-intro{
            margin-top: 10px;
            font-size: .7rem;
            text-align: center;

            @media screen and (max-width:480px) {
                text-align: left;
            }

            @media screen and (max-width:350px){
                display: none;
            }
        }

        .member-about{
            font-size: .7rem;
        }

        .member-social{
            margin-top: 10px;

            position: absolute;
            bottom: 20px;
            

            .social-icon{
                font-size: 1.2rem;
                margin-right: 8px;
            }

            .card-link{
                text-decoration: none;
                color: rgb(180, 180, 180);
            }

            @media screen and (max-width:480px) {
                right: 0; 
                bottom: 0;   
            }
        }
    }
}

.team-member-card:hover{
    .card-side-line{
    //  transform: translateX(0%);
        width: 100%;
    }

    .member-avatar{
        // border: 2px solid yellow;
    }

    @media screen and (max-width:480px) {
            .card-side-line{
            //  transform: translateX(0%);
                width: 20%;
            }
        
            .member-avatar{
                // border: 2px solid yellow;
            }
        
    }
}