.secnavbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 5px 60px;
  transition: 0.35s ease-in-out 0s;

  width: 100%;
  position: fixed;
  // background-color: #1a1a1a00;
  background-color: #1a1a1a;

  // background-image: linear-gradient(to bottom , #353e4b , rgba(255, 255, 255, 0));

  .logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    text-transform: uppercase;
    text-decoration: none;

    font-family: Lato, sans-serif;
    font-weight: 400;
    color: rgb(255, 255, 255);

    .logo-img {
      height: 70px;
      // filter: brightness(0%);
      padding: 10px 0;
      margin-right: 10px;
    }
  }

  .nav-container {
    .nav-ul {
      display: flex;
      align-items: center;
      justify-content: center;

      font-family: Montserrat, sans-serif;

      li {
        list-style: none;
        text-transform: uppercase;

        // margin: 0 10px;

        padding: 25px 10px;

        display: flex;
        align-items: center;
        justify-content: center;

        .nav-link {
          text-decoration: none;
          font-size: 0.725rem;
          color: rgb(255, 255, 255);
          z-index: 99;

          cursor: pointer;
        }

        .nav-arrow {
          display: none;
        }
      }

      .sub-nav {
        .dropdown {
          display: flex;
          transform: translateX(120%);
          position: absolute;
          // z-index: 0;
          // z-index: 101;
          top: 0px;
          right: 0px;
          // margin-top: 69.5px;
          // background-color: #00b5e2;
          background-color: #fff;
          // border-radius: 10px;
          min-width: 650px;
          height: 200vh;
          // height: calc(200vh - 69.5px);
          padding: 15px 30px;
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.048);
          overflow: hidden;

          // opacity: 0;
          transition: 0.2s ease-in-out 0s;

          flex-direction: column;
          align-items: flex-start;
          justify-content: flex-start;

          .sub-nav-heading {
            margin-top: 105px;
            font-size: 1.4rem;

            // transform: translateX(220%);
            // transition: .2s ease-in-out 0s;
          }

          .underline {
            height: 0.2rem;
            margin: 10px 0 20px 0;
            width: 80px;
            background-color: #00b5e2;

            // transform: translateX(220%);
            // transition: .2s ease-in-out 0s;
          }

          li {
            margin: 5px 0;
            list-style: none;
            padding: 0 0;

            // transform: translateX(220%);
            // transition: .2s ease-in-out 0s;

            .sub-nav-link {
              text-decoration: none;
              padding: 15px 15px;
              width: 325px;
              background-color: rgb(247, 247, 247);
              color: #000;

              display: flex;
              align-items: center;
              justify-content: flex-start;

              .nav-icon {
                font-size: 35px;
                // height: 35px;
              }

              .sub-nav-name {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;

                padding-left: 10px;

                font-size: 0.9rem;

                .arrow {
                  font-size: small;
                  margin-left: 0px;
                  transition: 0.1s ease-in-out 0s;
                  opacity: 0;
                }

                .sub-nav-info {
                  font-size: 0.7rem;
                  padding-top: 3px;
                  text-transform: capitalize;
                }
              }
            }
            .sub-nav-link:hover {
              background-color: #00b5e2;
              color: #fff;
            }
            .sub-nav-link:hover .arrow {
              opacity: 1;
              margin-left: 30px;
            }
          }
        }
      }
      .sub-nav:hover .dropdown {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        transform: translateY(0%);
        z-index: 10;
        opacity: 1;

        .sub-nav-heading {
          transform: translateX(0%);
        }

        .underline {
          transform: translateX(0%);
        }

        li {
          transform: translateX(0%);
        }
      }

      .sub-nav:hover {
        .nav-link {
          font-weight: 700;

          // border-bottom: 2px solid rgb(0, 0, 0);
        }
        // border-bottom: 2px solid #fff;
      }
    }

    .nav-ul:hover .dropdown {
      transform: translateY(0%);
      // z-index: 101;
    }

    .nav-ul:hover > li .nav-link {
      color: #000;
    }
  }

  .burger {
    display: none;
  }
}

.colorChange {
  background-image: unset;
  // background-color: #0d1623;
  padding: 0px 60px;
}

.heightChange {
  // margin-top: 89.5px!important;
}

@media screen and (max-width: 790px) {
  .heightChange {
    margin-top: unset !important;
  }
  .secnavbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
    padding: 0 30px;

    width: 100%;
    position: fixed;
    top: 0px;

    background-image: unset;
    background-color: #1a1a1a;

    .logo {
      display: flex;
      align-items: center;
      justify-content: flex-start;

      text-transform: uppercase;
      text-decoration: none;

      font-family: lato, sans-serif;
      font-weight: 400;
      font-size: 0.7rem;
      color: #fff;

      .logo-img {
        height: 70px;

        margin-right: 10px;
      }
    }

    .nav-container {
      overflow-y: scroll;
      position: absolute;
      top: 0;
      right: 0;
      height: 100vh;
      width: 70%;
      background-color: #fff;

      transform: translateX(100%);
      transition: 0.25s ease-in-out 0s;
      box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
      // padding-top: 70px;

      .nav-ul {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        font-family: Montserrat, sans-serif;
        background-color: #fff;
        margin-top: 70px;
        padding-bottom: 40px;

        li {
          list-style: none;
          text-transform: uppercase;

          // margin: 0 10px;

          padding: 5px 10px;

          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: center;

          .nav-link {
            text-decoration: none;
            font-size: 0.85rem;
            color: #000;

            padding: 15px 15px;

            .nav-arrow {
              display: inline;
              font-size: small;
              position: absolute;
              right: 20px;
              transform: rotateZ(90deg);
            }
          }
        }

        .sub-nav {
          width: 100%;

          height: unset;

          .dropdown {
            width: 100%;
            display: unset;
            transform: translateX(0%);
            position: relative;
            z-index: 0;
            top: 0px;
            right: 0px;
            margin-top: 0px;
            // background-color: #00b5e2;
            background-color: #fff;
            // border-radius: 10px;
            min-width: unset;
            height: unset;
            padding: 0px 0px;
            box-shadow: unset;
            overflow: hidden;

            transition: 0.2s ease-in-out 0s;

            .sub-nav-heading {
              display: none;
              margin-top: 55px;
              font-size: 1.4rem;
            }

            .underline {
              display: none;
              height: 0.2rem;
              margin: 10px 0 20px 0;
              width: 80px;
              background-color: #00b5e2;
            }

            li {
              margin: 5px 0;
              list-style: none;
              padding: 0 0;

              .sub-nav-link {
                text-decoration: none;
                padding: 15px 15px;
                width: 100%;
                background-color: rgb(247, 247, 247);
                color: #000;

                display: flex;
                align-items: center;
                justify-content: flex-start;

                .nav-icon {
                  font-size: 25px;
                  // height: 35px;
                }

                .sub-nav-name {
                  display: flex;
                  flex-direction: column;
                  align-items: flex-start;
                  justify-content: center;

                  padding-left: 10px;

                  font-size: 0.85rem;

                  .arrow {
                    display: none;
                    font-size: small;
                    margin-left: 0px;
                    transition: 0.1s ease-in-out 0s;
                    opacity: 0;
                  }

                  .sub-nav-info {
                    font-size: 0.65rem;
                    padding-top: 3px;
                    text-transform: capitalize;
                  }
                }
              }
              .sub-nav-link:hover {
                background-color: #00b5e2;
                color: #fff;
              }
              .sub-nav-link:hover .arrow {
                opacity: 1;
                margin-left: 30px;
              }
            }
          }
        }

        .sub-nav:hover .dropdown {
          display: unset;
          flex-direction: column;
          align-items: flex-start;
          justify-content: flex-start;
          transform: translateY(0%);
        }

        .sub-nav:hover {
          font-weight: unset;
          // border-bottom: 2px solid #fff;
        }
      }
      .nav-ul:hover > li .nav-link {
        color: unset;
      }
    }

    .burger {
      display: block;
      z-index: 200;

      .line-1,
      .line-2,
      .line-3 {
        width: 25px;
        height: 3px;
        background-color: rgb(197, 197, 197);
        margin: 5px;
        transition: all 0.3s ease;
      }

      .toggle .line-1 {
        transform: rotate(-45deg) translate(-5px, 6px);
      }
      .toggle .line-2 {
        opacity: 0;
      }
      .toggle .line-3 {
        transform: rotate(45deg) translate(-5px, -6px);
      }
    }
  }

  .nav-active {
    transform: translateX(0%) !important;
  }
}

@media screen and (max-width: 450px) {
  .secnavbar {
    .logo {
      font-size: 0.6rem;
      padding: 5px 0;
      .logo-img {
        height: 55px;

        margin-right: 10px;
      }
    }

    .nav-container {
      .nav-ul {
        li {
          .nav-link {
            font-size: 0.765rem;
          }
        }

        .sub-nav {
          width: 100%;

          height: unset;

          .dropdown {
            li {
              .sub-nav-link {
                .sub-nav-name {
                  font-size: 0.65rem;

                  .sub-nav-info{
                      font-size: .6rem;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
