#section-one-verify{
    .main-container{
        min-height: 100vh;
        // margin-bottom: 100px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        padding: 175px 60px 60px 60px;
        color: #505050;

        

        .section-title{
            color: #505050;
        }

        .underline{
            height: .15rem;
            width: 60px;
            background-color: tomato;
            margin: 5px 0;
        }

        .underline-small{
            height: .12rem;
            width: 30px;
            background-color: tomato;
            margin: 5px 0;
        }

        .section-subtitle{
            margin-top: 30px;
        }

        .paragraph-top{
            margin-top: 10px;
        }

        .section-info{
            margin-top: 15px;

            // width: 80%;
            text-align: justify;
        }

        p{
            font-size: .95rem;
            line-height: 1.6rem;
            @media screen and (max-width:420px) {
                font-size: .9rem;
                line-height: 1.4rem;
            }

            @media screen and (max-width:390px) {
                font-size: .85rem;
                line-height: 1.4rem;
            }

            @media screen and (max-width:360px) {
                font-size: .75rem;
                line-height: 1.2rem;
            }
        }

        .text-field{
            margin-top: 15px;
            display: flex;
            align-items: center;
            justify-content: center;

            Button{
                height: 56px;
                margin-left: 10px;
            }
        }

        .left{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
        }
    
        .right{
            // width: 100%;
            padding: 50px 0;
            .table{
                min-width: 600px;

                @media screen and (max-width: 600px) {
                    min-width: 100%;
                }
            }
        }
    }

    

}