.subscribe-form {
    position: relative;
    height: 100%;
    width: 100%;
  
    display: flex;
    align-items: center;
    justify-content: center;
  
    .form-control {
      position: relative;
      height: 100%;
      width: 100%;

      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: center;

      @media screen and (max-width:420px) {
        flex-direction: column;
        align-items: center;
      }
    }
  
    .text-field {
    //   width: 350px;
    width: 65%;

    @media screen and (max-width:420px) {
      width: 70%;
    }
      // background-color: #f3f1f1;
      
  
      @media screen and (max-width:420px) {
          width: 100%;
      }
  
      label {
        color: #505050;
      }
  
      input {
        color: #505050;
      }
  
      textarea {
        color: #505050;
        
      }
    }
  
    .send-button {
    //   position: absolute;
    //   bottom: 0;
    //   right: 0;
      color: #505050;
      height: 56px;
      margin-left: 10px;
      // border-color: #505050;
  
      .arrow {
        margin-left: 10px;
        color: tomato;
      }

      @media screen and (max-width:420px) {
        margin-top: 20px;
      }
    }
  
    .reset-button {
      position: absolute;
      left: 0;
      bottom: 0;
      // color: #505050;
    }
  }
  
  
  #success-subscribe-form{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      height: 100%;
      width: 100%;
  
      .form-reset-button{
          // position: absolute;
          bottom: 0;
          right: 0;
          // color: #505050;
      }
  }