#section-one-contact {
  height: 100vh;
  //   padding: 100px 0;
  position: relative;
  // z-index: 200;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  // @media screen and (max-width: 1920px) {
  //   height: 700px;
  // }

  .top-block{
    position: absolute;
    top: 0;
    width: 100%;
    height: 80px;
    background-color: #0d1623;
    @media screen and (max-width:420px) {
      height: 60px;
    }
  }

  .main-container {
    //   height: 100vh;
    height: 100%;
    width: 100%;
    // background-color: #1a1a1a;
    // background-image: linear-gradient(to right , #232526 , #414345);
    // padding: 100px 0;

    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    // color: rgb(226, 224, 224);
    color: #505050;
    // padding-bottom: 100px;
    .styled-character-position {
      // width: 210px;
      right: 50%;
      top: 35px;
      display: none;

      @media screen and (max-width: 600px) {
        margin-left: 0px;
      }

      @media screen and (max-width: 420px) {
        right: 0px;
        transform: rotateZ(-90deg);
        top: 100px;
        margin-left: 0;
      }
    }

    .underline {
      height: 0.15rem;
      width: 60px;
      margin: 5px 0;
      background-color: tomato;
      z-index: 1;
    }

    .underline-small {
      height: 0.12rem;
      width: 30px;
      margin: 5px 0;
      background-color: tomato;
    }

    .left {
      height: 100%;
      width: 50%;

      padding: 0 60px 0 60px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      position: relative;

      .block {
        position: absolute;
        left: 0;
        height: 350px;
        width: 120px;
        // background-color: #ececec;
        .sub-block {
          height: 100%;
          width: 5px;
          // background-color: rgb(255, 114, 71);
        }
      }

      .section-title {
        // margin-top: 100px;
        font-size: 3.8rem;
        z-index: 2;
        line-height: 5.6rem;
        .change-color {
          text-transform: capitalize;
          font-size: 6rem;
          color: tomato;
          // color: rgb(255, 208, 0);
          // font-family: 'Roboto', sans-serif;
        }
      }

      .section-subtitle {
        font-weight: 700;
        font-size: 1rem;
        line-height: 1.6rem;
        margin-top: 20px;
        // width: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;

        .arrow {
          transform: rotateZ(180deg);
          height: 35px;
          margin-right: 20px;
        }
      }

      .info{
        margin-top: 20px;
        line-height: 1.6rem;
      }

      .info-box {
        bottom: 10px;
        left: 0px;
        height: 115px;
        width: 95px;
        position: absolute;
        background-color: var(--secondaryColor);

        .box-left {
          left: 60px;
          width: 225px;
        }

        .box-right {
          right: -250px;
        }

        .arrow {
          font-size: 2.8rem;
          padding: 8px;
          border: 0.15rem solid tomato;
          border-radius: 100%;
        }
      }
    }

    .right {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50%;
      position: relative;
      padding: 0 60px;
      // background-color: #2a2a2a;
      .form-container {
        margin-top: 50px;
        height: 430px;
        width: 360px;
        background-color: #fafafa;
        // border: .15rem solid #d1cdcd;
        // background-image: linear-gradient(to right , #232526 , #414345);
        border-radius: 4px;
        // box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%),
        //   0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);

        // background-color: #1a1a1a;
        padding: 30px 30px;

        position: relative;

        display: flex;
        align-items: center;
        justify-content: center;

        .plane {
          position: absolute;
          height: 60%;
          opacity: 0.5;
        }

         .border-top-left{
          position: absolute;
          left: 0;
          top: 0;
          height: 20px;
          width: 3px;
          background-color: tomato;
        }

        .border-top-right{
          position: absolute;
          left: 0;
          top: 0;
          height: 3px;
          width: 20px;
          background-color: tomato;
        }

        .border-bottom-left{
          position: absolute;
          right: 0;
          bottom: 0;
          height: 20px;
          width: 3px;
          background-color: tomato;
        }

        .border-bottom-right{
          position: absolute;
          right: 0;
          bottom: 0;
          height: 3px;
          width: 20px;
          background-color: tomato;
        }

      }
    }
  }
}

@media screen and (max-width: 965px) {
  #section-one-contact {
    height: unset;
    .main-container {
      flex-direction: column;
      .left {
        width: 100%;
        // height: 100vh;
        padding: 250px 60px 0 60px;

        @media screen and (max-width: 420px) {
          padding: 250px 30px 0 30px;
        }
      }
      .right {
        width: 100%;
        padding: 100px 60px;

        @media screen and (max-width: 420px) {
          padding: 100px 30px;
        }

        .form-container {
          width: 100%;
          padding: 30px 30px;
        }
        // height: 100vh;
      }
    }
  }
}
